html,
body {
	height: 100%;
}

body{
	 font-family: "Open Sans", sans-serif;
	font-size: 16px;
	line-height: 1.687;
	font-weight: 400;
	color: $default;
	overflow-x: hidden;
	min-width: 300px;
}
.none {
  display: none !important;
}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}

.main {
	flex: 1 0 auto;
}
.footer {
	flex: 0 0 auto;
}

.container{
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}
.container-fluid{
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	padding: 0 15px;

}

.slick-dots{
	display: flex;
	justify-content: center;
	align-items: center;
	li{
		margin: 0 15px;
	}
	button{
		background-color: $title;
		border: none;
		width: 8px;
		height: 8px;
		border-radius: 50%;
		padding: 0;
		font-size: 0;
	}
	li.slick-active{
		button{
			background-color: #FFEF35;
		}
	}
}

  .info-email{
    font-weight: 600;
    font-size: 14px;
    color: $title;
    display: inline-block;
    margin-bottom: 22px;
    padding-left: 30px;
    position: relative;
    &::before{
      position: absolute;
      content: '';
      background-image: url('../images/envelope.svg');
      top: 8px;
      left: 0;
      width: 16px;
      height: 12px;
    }
  }





